export default {
	data(){
		return {
			showStatus: false
		}
	},
	props: {
		show: {
			type: Boolean,
			default: false
		}
	},
	watch: {
		show(value){
			console.log(value)
		}
	},
	methods: {
		showOrHide(key){
			if(key){
				console.log(this)
				this.$refs.noticeLogin.open()
				return
			}
			uni.showToast({
				icon:'none',
				title:'当前组件查找失败'
			})
		},
		getPhoneNumber(e){
			const { iv, encryptedData, errMsg } = e.detail
			if( errMsg !== 'getPhoneNumber:ok' ){
				uni.showToast({
					title:'授权失败，请重新授权'
				})
			}
			// 以下执行登录,需要后台提供一个接口
			uni.login({
				success(response) {
					if(response.errMsg === ''){
						
						// 此处执行后台登录过程，传递的参数自己斟酌
						userModel.wxLogin({
							data:{
								encryptedData,
								ivData: iv,
								code: response.code
							},
							success(res){
								console.log(res)
								// 此处应返回前端信息
								if (res.data.code == 0) {
															  
								}else{
									uni.showToast({
										title:'登录失败，请重新授权'
									})
								}
								
							}
						})	
					}else{
						uni.showToast({
							title:'获取code失败'
						})
					}
				}
			})
		},
	}
}
